import { useRef } from "react";
import { styled } from "@stitches/react";
import useOnScreen from "../hooks/useOnScreen";
import { ConfigsType } from "../configs";

const isPortrait = window.matchMedia("(orientation: portrait)").matches;

const Layout = styled("div", {
  width: "100%",
  padding: isPortrait ? "15% 0% 15% 5%" : "5% 0% 5% 10%",
});

const Title = styled("p", {
  color: "#795548",
  width: "100%",
  fontSize: isPortrait ? "2.5em" : "3.5em",
  margin: 0,
  fontWeight: "500",
  textAlign: "center",
});

const SubTitle = styled("p", {
  color: "#795548",
  width: "100%",
  fontSize: isPortrait ? "1.0em" : "2em",
  margin: "24px 0",
  fontWeight: "300",
  textAlign: "center",
  lineHeight: 1.8,
});

const ExclamationMarkTitle = styled("span", {
  color: "#795548",
  width: "100%",
  fontSize: isPortrait ? "1.4em" : "2.2em",
  fontWeight: "500",
  lineHeight: 1.8,
});

type GreetingProps = {
  config: ConfigsType;
};

const Greeting = ({ config }: GreetingProps) => {
  const ref = useRef<HTMLSelectElement>(null);
  const onScreen: boolean = useOnScreen<HTMLDivElement>(ref, "-125px");

  return (
    <section
      ref={ref}
      style={{
        height: "60vh",
        background: onScreen ? "#EFEBE9" : "#DADADA",
        overflow: "hidden",
        position: "relative",
        transition: "background 1s ease-in",
      }}
    >
      <Layout>
        <Title></Title>
        <SubTitle>
          <span>
                  <ExclamationMarkTitle></ExclamationMarkTitle>  .
          </span>
          <br />
           <ExclamationMarkTitle></ExclamationMarkTitle>     .
          <br />
            <ExclamationMarkTitle></ExclamationMarkTitle>  
          <br />
          <ExclamationMarkTitle></ExclamationMarkTitle>    .
          <br />
          <br />
                .
          <br />
                 
          <br />
          <br />
          {config.groom.fatherName}  {config.groom.motherName} {" "}
          {config.groom.name}
          <br />
          {config.bride.fatherName}  {config.bride.motherName} {" "}
          {config.bride.name}
        </SubTitle>
      </Layout>
    </section>
  );
};

export default Greeting;
